* {
  box-sizing: border-box; }

html,
body {
  height: 100%; }

body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 400;
  font-style: normal;
  color: #2e3a59; }

ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none; }

.container {
  width: 100%;
  background-color: #fff;
  position: relative; }

h1,
h2,
h3,
h4,
p,
a,
ul,
li,
ol {
  margin: 0;
  padding: 0; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header__wrap {
  padding-top: 11px;
  padding-right: 57px;
  padding-left: 47px;
  padding-bottom: 15px; }

.header__logo {
  display: block;
  width: 216px;
  height: 63px;
  margin: 0 auto; }

.header__nav {
  display: none; }

@media (min-width: 768px) {
  .header__wrap {
    padding-top: 30px;
    padding-right: 84px;
    padding-left: 87px; }
  .header__logo {
    display: block;
    width: 318px;
    height: 92px;
    margin: 0; }
  .header__nav {
    display: inline-block; } }

@media (min-width: 1140px) {
  .header {
    background-color: #ffffff; }
  .header__logo {
    display: inline-block; }
  .header__nav {
    display: inline-block;
    margin-left: 65px; } }

.main-nav__list {
  display: inline-block; }

.main-nav__item {
  float: left;
  line-height: 92px; }

.main-nav__item:not(:first-child) {
  margin-left: 36px; }

.main-nav__link {
  position: relative;
  color: #A6ABBD;
  text-decoration: none;
  padding-top: 6px;
  padding-right: 24px;
  padding-bottom: 6px;
  padding-left: 24px; }

.main-nav__link:hover {
  box-shadow: 0px 8px 16px rgba(0, 195, 104, 0.4);
  border-radius: 8px;
  background-color: #00C368;
  color: white; }

.main-nav__button-wrapper {
  display: none;
  float: right;
  padding-top: 30px; }

.main-nav__button {
  display: inline-block;
  width: 117px;
  height: 32px;
  cursor: pointer;
  border: none;
  box-shadow: 0px 8px 16px rgba(0, 195, 104, 0.4);
  border-radius: 8px;
  background-color: #00C368;
  color: white;
  outline: none; }

@media (min-width: 768px) {
  .main-nav__button-wrapper {
    display: block; } }

.intro {
  padding-top: 200px;
  background: url("../img/intro-bg-mobile@1x.png") no-repeat 50% 0; }

.intro__title {
  margin-top: 0;
  margin-bottom: 50px;
  font-size: 24px;
  line-height: 28px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  color: #2e3a59; }

.intro__features {
  display: none; }

@media (min-width: 1140px) {
  .intro {
    padding-top: 75px;
    padding-left: 75px;
    padding-right: 75px;
    position: relative;
    min-height: 947px;
    background: linear-gradient(111.04deg, #e2e2e2 0.39%, rgba(255, 255, 255, 0) 86.46%);
    background-size: 100%; }
  .intro__bg {
    position: absolute;
    z-index: 10;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: url("../img/balls-left.png") no-repeat 0 50%, url("../img/balls-right.png") no-repeat 100% 150px;
    background-size: 442px 447px, 400px 442px;
    background-attachment: fixed; }
  .intro__title {
    position: relative;
    z-index: 10;
    font-size: 40px;
    line-height: 47px;
    padding-top: 20px; }
  .intro__title:before {
    position: absolute;
    top: 0;
    content: "";
    left: 50%;
    transform: translateX(-50%);
    min-width: 184px;
    height: 4px;
    background-color: #00C368; }
  .intro__slider {
    position: relative;
    z-index: 20; }
  .intro__features {
    position: relative;
    z-index: 20;
    display: block; }
  .intro__features-row {
    display: table;
    direction: ltr; }
  .intro__features-item {
    display: table-cell; }
  .intro__features-row:nth-child(1),
  .intro__features-row:nth-child(3) {
    width: 465px;
    margin: 0 auto; }
  .intro__features-row:nth-child(2) {
    width: 850px;
    margin: 0 auto; }
  .intro__features-row:nth-child(2) .intro__features-item {
    vertical-align: middle; }
  .intro__features-row:nth-child(1) .intro__features-item,
  .intro__features-row:nth-child(3) .intro__features-item {
    width: 50%; }
  .intro__features-item--picture {
    width: 436px; }
  .intro__features-icon {
    display: block;
    margin: 0 auto; }
  .intro__features-text {
    text-align: center; }
  .intro__features-row:nth-child(2) .intro__features-item:nth-child(1) .intro__features-icon {
    width: 56px;
    height: 72px; }
  .intro__features-row:nth-child(2) .intro__features-item:nth-child(3) .intro__features-icon {
    width: 96px;
    height: 87px; }
  .intro__features-icon:hover path,
  .intro__features-icon:hover rect,
  .intro__features-icon:hover circle {
    stroke: #00C368; } }

.slider__item-icon:hover path,
.slider__item-icon:hover rect,
.slider__item-icon:hover circle {
  stroke: #00C368; }

.slider__item-icon {
  display: block;
  margin: 0 auto;
  width: 64px;
  height: 72px; }

.slider__item-text {
  text-align: center;
  padding: 0 10px; }

.bx-wrapper {
  box-shadow: none;
  margin-bottom: 85px; }

.bx-wrapper .bx-pager.bx-default-pager a {
  width: 8px;
  height: 8px;
  background: #e7ebf0; }

.bx-wrapper .bx-pager.bx-default-pager a:hover,
.bx-wrapper .bx-pager.bx-default-pager a.active {
  background: #a6abbd; }

.bx-wrapper .bx-controls-direction a {
  top: auto;
  bottom: -42px; }

.bx-wrapper .bx-controls-direction a.bx-prev {
  left: calc(50% - 90px);
  background: url("../img/arrow_left.png") no-repeat 12px 6px;
  background-size: 11px 19px; }

.bx-wrapper .bx-controls-direction a.bx-next {
  right: calc(50% - 90px);
  background: url("../img/arrow_right.png") no-repeat 8px 6px;
  background-size: 11px 19px; }

@media (min-width: 1140px) {
  .slider {
    display: none; } }

.features {
  padding-top: 40px;
  padding-right: 16px;
  padding-bottom: 9px;
  padding-left: 16px;
  background: #F8FBFF;
  color: #2e3a59;
  direction: ltr; }

.features__title {
  position: relative;
  margin-top: 0;
  margin-bottom: 30px;
  padding-top: 20px;
  font-size: 24px;
  line-height: 28px;
  font-weight: 700;
  text-align: center; }

.features__title:before {
  position: absolute;
  top: 0;
  content: '';
  left: 50%;
  transform: translateX(-50%);
  min-width: 184px;
  height: 4px;
  background-color: #00C368; }

.features__text {
  margin-top: 0;
  margin-bottom: 30px;
  text-align: center; }

@media (min-width: 768px) {
  .features {
    width: 50%;
    display: table-cell;
    direction: ltr; } }

@media (min-width: 1140px) {
  .features__title {
    font-size: 40px;
    line-height: 47px; } }

.accordion__item:not(:last-child) {
  border-bottom: 1px solid #E7EBF0; }

.accordion__title {
  padding-top: 15px;
  padding-bottom: 25px;
  padding-right: 30px;
  font-size: 16px;
  line-height: 19px;
  font-weight: 700;
  background: url("../img/arrow_down.png") no-repeat 100% 20px;
  background-size: 19px 10px;
  cursor: pointer; }

.accordion__title--active {
  background: url("../img/arrow_up.png") no-repeat 100% 20px;
  background-size: 19px 10px;
  color: #00C368; }

.accordion__content {
  display: none; }

.accordion__content--active {
  display: block; }

@media (min-width: 1140px) {
  .accordion__title {
    position: relative;
    padding-left: 64px;
    font-size: 24px;
    line-height: 28px; }
  .accordion__title:before {
    position: absolute;
    content: '';
    left: 0;
    width: 40px;
